import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchNftData } from "./redux/nftData/nftDataActions";
import { fetchTokenData } from "./redux/tokenData/tokenDataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";
import Countdown from 'react-countdown';
import { BrowserView, MobileView, isBrowser, isMobile } from 'react-device-detect';


const truncate = (input, len) =>
  input.length > len ? `${input.substring(0, len)}...` : input;

const Completionist = () => "";

export const StyledButton = styled.button`
  padding: 10px;
  border-radius: 50px;
  border: none;
  background-color: var(--buttons);
  padding: 10px;
  font-weight: bold;
  color: var(--button-text);
  width: 100px;
  cursor: pointer;
  box-shadow: 0px 6px 0px -2px var(--button-shadow);
  -webkit-box-shadow: 0px 6px 0px -2px var(--button-shadow);
  -moz-box-shadow: 0px 6px 0px -2px var(--button-shadow);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const StyledRoundButton = styled.button`
  padding: 10px;
  border-radius: 100%;
  border: none;
  background-color: var(--buttons);
  padding: 10px;
  font-weight: bold;
  font-size: 15px;
  color: var(--button-text);
  width: 40px;
  height: 40px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 0px -2px var(--button-shadow);
  -webkit-box-shadow: 0px 4px 0px -2px var(--button-shadow);
  -moz-box-shadow: 0px 4px 0px -2px var(--button-shadow);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const StyledLogo = styled.img`
  width: 200px;
  @media (min-width: 767px) {
    width: 300px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
  margin-top: 1em;
`;

export const StyledImg = styled.img`
  // box-shadow: 0px 4px 0px -2px rgba(250, 50, 250, 0.3);
  border: none;
  background-color: var(--box-bg);
  border-radius: 100%;
  width: 200px;
  @media (min-width: 900px) {
    width: 250px;
  }
  @media (min-width: 1000px) {
    width: 300px;
  }
  transition: width 0.5s;
`;

export const StyledLink = styled.a`
  color: var(--highlighted-text);
  text-decoration: none;
`;


function App() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const nftData = useSelector((state) => state.nftData);
  const tokenData = useSelector((state) => state.tokenData);
  const [claimingNft, setClaimingNft] = useState(false);
  const [approvingToken, setApprovingToken] = useState(false);
  const [approvedToken, setApprovedToken] = useState(false);
  const [feedback, setFeedback] = useState(`Please select how many NFTs you want to mint in one transcation.`);
  const [mintAmount, setMintAmount] = useState(10);
  const [CONFIG, SET_CONFIG] = useState({
    NFT_CONTRACT_ADDRESS: "",
    TOKEN_CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY_BCH: 1,
    MAX_SUPPLY_NFTC: 1,
    MAX_MINT_PER_TX: 1,
    WEI_COST_BCH: 0,
    DISPLAY_COST_BCH: 0,
    WEI_COST_NFTC: 0,
    DISPLAY_COST_NFTC: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
    LAUNCH_DATE: "",
    END_DATE: "",
  });


  const claimNFTsWithBCH = () => {
    let costBch = CONFIG.WEI_COST_BCH;

    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostBchWei = String(BigInt(costBch * mintAmount));
    let totalGasLimit = String(BigInt(gasLimit * mintAmount));
    console.log("CostBch: ", totalCostBchWei);

    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`Minting your ${CONFIG.NFT_NAME} NFTs. Please wait!`);
    setClaimingNft(true);
    blockchain.nftSmartContract.methods
      .mint(mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.NFT_CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostBchWei,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("An error occured. " + err + "\nPlease try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `Congratulations! You successfully minted some ${CONFIG.NFT_NAME} NFTs! Visit ${CONFIG.MARKETPLACE} to take a look at your NFTs.`
        );
        setClaimingNft(false);
        dispatch(fetchNftData(blockchain.account));
      });
  };


  const approveNftc = () => {
    let costNftc = CONFIG.WEI_COST_NFTC;
    let totalCostNftcWei = String(BigInt(costNftc * mintAmount + 10 ** 18));

    let totalGasLimit = String(70000);

    setFeedback(`Approving NFTC...`);
    setApprovingToken(true);

    blockchain.tokenSmartContract.methods
      .approve(CONFIG.NFT_CONTRACT_ADDRESS, totalCostNftcWei)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.TOKEN_CONTRACT_ADDRESS,
        from: blockchain.account,
        value: 0,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("An error occured: " + err + "\nPlease try again later.");
        setApprovingToken(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `NFTC approved. Now you can use it to mint your ${CONFIG.NFT_NAME} NFTs!`
        );
        setApprovingToken(false);
        setApprovedToken(true);
        dispatch(fetchTokenData(blockchain.account, CONFIG.NFT_CONTRACT_ADDRESS));
      });
  };

  const claimNFTsWithNFTC = async () => {
    let costNftc = CONFIG.WEI_COST_NFTC;
    let totalCostNftcWei = String(BigInt(costNftc * mintAmount));
    console.log("CostNftc: ", totalCostNftcWei);

    if (blockchain.account !== "" && blockchain.nftSmartContract !== null) {
      await dispatch(fetchTokenData(blockchain.account, CONFIG.NFT_CONTRACT_ADDRESS));
      if (BigInt(tokenData.tokensApproved) < BigInt(totalCostNftcWei)) {
        setFeedback(`Not enough NFTC approved for minting ` + mintAmount + ` ${CONFIG.NFT_NAME} NFTs!`);
        setApprovedToken(false);
        return false;
      }
      else if (BigInt(tokenData.tokenBalance) < BigInt(totalCostNftcWei)) {
        setFeedback(`You don't own enough NFTC for minting ` + mintAmount + ` ${CONFIG.NFT_NAME} NFTs!`);
        setApprovedToken(false);
        return false;
      }
      else {
        setApprovedToken(true);
      }
    }

    let gasLimit = CONFIG.GAS_LIMIT;
    let totalGasLimit = String(BigInt(gasLimit * mintAmount));

    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`Minting your ${CONFIG.NFT_NAME} NFTs...`);
    setClaimingNft(true);
    blockchain.nftSmartContract.methods
      .mintWithNftc(mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.NFT_CONTRACT_ADDRESS,
        from: blockchain.account,
        value: 0,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("An error occured: " + err + "\nPlease try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `Congratulations! You successfully minted some ${CONFIG.NFT_NAME} NFTs! Visit ${CONFIG.MARKETPLACE} to take a look at your NFTs.`
        );
        setClaimingNft(false);
        dispatch(fetchTokenData(blockchain.account, CONFIG.NFT_CONTRACT_ADDRESS));
      });
  };

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > CONFIG.MAX_MINT_PER_TX) {
      newMintAmount = CONFIG.MAX_MINT_PER_TX;
    }
    setMintAmount(newMintAmount);
  };

  const setMintAmountInput = (e) => {
    let newMintAmount = parseInt(e.target.value);
    if (newMintAmount > CONFIG.MAX_MINT_PER_TX) {
      newMintAmount = CONFIG.MAX_MINT_PER_TX;
    }
    else if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const getNftData = () => {
    if (blockchain.account !== "" && blockchain.nftSmartContract !== null) {
      dispatch(fetchNftData(blockchain.account));
    }
  };

  const getNftAndTokenData = () => {
    if (blockchain.account !== "" && blockchain.nftSmartContract !== null) {
      dispatch(fetchTokenData(blockchain.account, CONFIG.NFT_CONTRACT_ADDRESS));
    }
  };

  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();
  }, []);

  useEffect(() => {
    getNftAndTokenData();
  }, [blockchain.account]);

  return (
    <s.Screen>
      <s.Container
        flex={1}
        ai={"center"}
        style={{ padding: 24, backgroundColor: "var(--site-bg)" }}
        image={CONFIG.SHOW_BACKGROUND ? (isMobile ? "/config/images/bgMobile.jpg" : "/config/images/bg.jpg") : null}
      >
        {/* <StyledLogo alt={"logo"} src={"/config/images/logo.jpg"} /> */}
        <ResponsiveWrapper flex={1} style={{ padding: 24 }} test>
          {/* <s.Container flex={1} jc={"center"} ai={"center"}>
            <StyledImg alt={"preview"} src={"/config/images/preview.gif"} />
          </s.Container>
          <s.SpacerLarge /> */}
          <s.Container
            flex={2}
            jc={"center"}
            ai={"center"}
            style={{
              backgroundColor: "var(--box-bg)",
              padding: 24,
              borderRadius: 24,
              border: "none",
              // boxShadow: "0px 4px 0px -2px rgba(250,50,250,0.3)",
            }}
          >
            <s.TextDescription
              style={{
                textAlign: "center",
                color: "var(--normal-text)",
                paddingBottom: "1em",
                maxWidth: "450px",
              }}
            ><img
                style={{
                  marginTop: "1em",
                  maxWidth: "100%",
                }}
                src="/config/images/logo.png"
                alt="Kensho" />

              <p
                style={{
                  marginTop: "1em",
                }}
              >Kensho is the latest high-quality NFT collection created by NFT Club. After minting your Kenshos, you can stake them at <StyledLink target={"_blank"} href="https://app.nftclub.cash">app.nftclub.cash</StyledLink> to generate passive income of NFTC tokens.
              </p>
              <p
                style={{
                  marginTop: "1em",
                }}
              >
                <s.SpacerLarge />

                <StyledImg alt={"preview"} src={"/config/images/preview.gif"} />
              </p>
            </s.TextDescription>
            <s.SpacerLarge />
            {new Date() < new Date(CONFIG.LAUNCH_DATE) ? (
              <s.TextTitle
                style={{
                  textAlign: "center",
                  fontSize: 24,
                  color: "var(--normal-text)",
                }}
              >
                Stay tuned! The {CONFIG.NFT_NAME} NFT collection is launching soon â€” only on SmartBCH!
              </s.TextTitle>
            ) : (
              <>
                <s.TextTitle
                  style={{
                    textAlign: "center",
                    fontSize: 32,
                    fontWeight: "bold",
                    fontFamily: "Liberation Mono",
                    color: "var(--highlighted-text)",
                  }}
                >
                  {nftData.mintedWithNftc} / {CONFIG.MAX_SUPPLY_NFTC} minted with NFTC
                </s.TextTitle>
                <s.TextTitle
                  style={{
                    textAlign: "center",
                    fontSize: 32,
                    fontWeight: "bold",
                    fontFamily: "Liberation Mono",
                    color: "var(--highlighted-text)",
                  }}
                >
                  {nftData.mintedWithBch} / {CONFIG.MAX_SUPPLY_BCH} minted with BCH
                </s.TextTitle>
              </>
            )}

            <s.SpacerSmall />
            <s.TextDescription
              style={{
                textAlign: "center",
                fontFamily: "Liberation Mono",
                color: "var(--normal-text)",
              }}
            >
              {CONFIG.TOKEN_CONTRACT_ADDRESS !== CONFIG.NFT_CONTRACT_ADDRESS ? (
                <StyledLink target={"_blank"} href={CONFIG.SCAN_LINK}>
                  {CONFIG.NFT_NAME} CA: {truncate(CONFIG.NFT_CONTRACT_ADDRESS, 15)}
                </StyledLink>
              ) : null}
            </s.TextDescription>
            <s.SpacerSmall />


            {Number(nftData.totalSupply) >= CONFIG.MAX_SUPPLY ? (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--big-text)" }}
                >
                  The sale has ended.
                </s.TextTitle>
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--big-text)" }}
                >
                  You can still find {CONFIG.NFT_NAME} on
                </s.TextDescription>
                <s.SpacerSmall />
                <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                  {CONFIG.MARKETPLACE}
                  <img style={{
                    width: "192px",
                  }} src="/oasis_logo.svg" alt="Oasis" />
                </StyledLink>
              </>
            ) : (
              <>
                <s.SpacerSmall />
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--highlighted-text)" }}
                >
                  2,000 {CONFIG.SYMBOL} can be minted for {CONFIG.DISPLAY_COST_NFTC}{" "}
                  NFTC per NFT.
                </s.TextTitle>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--highlighted-text)" }}
                >
                  10,000 {CONFIG.SYMBOL} can be minted for {CONFIG.DISPLAY_COST_BCH}{" "}
                  {CONFIG.NETWORK.SYMBOL} per NFT.
                </s.TextTitle>

                <s.SpacerLarge />
                {blockchain.account === "" ||
                  blockchain.nftSmartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--normal-text)",
                      }}
                    >
                      Please connect MetaMask to the {CONFIG.NETWORK.NAME} network and then connect to this website.
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--normal-text)",
                      }}
                    >
                      If you are on a computer, please unlock your MetaMask browser extension first and then refresh this page before clicking the CONNECT button.
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--normal-text)",
                      }}
                    >
                      You can mint up to {CONFIG.MAX_MINT_PER_TX} NFTs per transaction. The NFTs are minted in a non-sequential, randomised order.
                    </s.TextDescription>

                    <s.SpacerLarge />
                    <s.SpacerSmall />
                    <StyledButton
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getNftAndTokenData();
                      }}
                    >
                      CONNECT
                    </StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--big-text)",
                          }}
                        >
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  <>
                    {new Date() > new Date(CONFIG.LAUNCH_DATE) ? (
                      <s.TextDescription
                        style={{
                          textAlign: "center",
                          color: "var(--big-text)",
                        }}
                      >
                        {feedback}
                      </s.TextDescription>
                    ) : null}


                    {/* {new Date() < new Date(CONFIG.END_DATE) ? ( */}
                    {new Date() > new Date(CONFIG.LAUNCH_DATE) ? (
                      <s.Container ai={"center"} jc={"center"} fd={"row"}>
                        <s.SpacerMedium />
                        <StyledRoundButton
                          style={{ lineHeight: 0.4 }}
                          disabled={claimingNft ? 1 : 0}
                          onClick={(e) => {
                            e.preventDefault();
                            decrementMintAmount();
                          }}
                        >
                          -
                        </StyledRoundButton>
                        <s.SpacerMedium />
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--highlighted-text)",
                          }}
                        >
                          <input type={"number"} style={{ width: "50px", padding: "5px" }} value={mintAmount} onChange={setMintAmountInput} />
                        </s.TextDescription>
                        <s.SpacerMedium />
                        <StyledRoundButton
                          disabled={claimingNft ? 1 : 0}
                          onClick={(e) => {
                            e.preventDefault();
                            incrementMintAmount();
                          }}
                        >
                          +
                        </StyledRoundButton>
                      </s.Container>
                    ) : null}
                    <s.SpacerSmall />
                    {/* {new Date() < new Date(CONFIG.END_DATE) ? ( */}
                    {new Date() > new Date(CONFIG.LAUNCH_DATE) ? (
                      <>
                        <s.Container ai={"center"} jc={"center"} fd={"row"}>
                          <StyledButton
                            disabled={claimingNft ? 1 : 0}
                            onClick={(e) => {
                              e.preventDefault();
                              claimNFTsWithBCH();
                              getNftData();
                            }}
                          >
                            {claimingNft ? "BUSY" : "MINT with BCH"}
                          </StyledButton>
                        </s.Container>
                        <s.SpacerLarge />

                        {/* <s.TextDescription
                                    style={{
                                      textAlign: "center",
                                      color: "var(--normal-text)",
                                    }}
                                  >
                                    NFTC token address: {CONFIG.TOKEN_CONTRACT_ADDRESS}
                                  </s.TextDescription> */}
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--normal-text)",
                          }}
                        >
                          Your NFTC balance: {Number(tokenData.tokenBalance) / 10 ** 18}
                        </s.TextDescription>


                        {/* <s.TextDescription
                                    style={{
                                      textAlign: "center",
                                      color: "var(--normal-text)",
                                    }}
                                  >
                                    NFTC approved: {Number(tokenData.tokensApproved) / 10 ** 18}
                                  </s.TextDescription> */}
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "var(--normal-text)",
                          }}
                        >
                          NFTs mintable with NFTC: {Math.floor(Number(tokenData.tokensApproved) / 10 ** 18 / 850)}
                        </s.TextDescription>
                        <s.SpacerSmall />

                        <s.Container ai={"center"} jc={"center"} fd={"row"}>
                          <s.SpacerMedium />
                          <StyledButton
                            disabled={approvedToken ? 1 : 0}
                            onClick={(e) => {
                              e.preventDefault();
                              approveNftc();
                            }}
                          >
                            {approvingToken ? "BUSY" : "APPROVE NFTC"}
                          </StyledButton>
                          <s.SpacerSmall />
                          <StyledButton
                            disabled={claimingNft ? 1 : 0}
                            onClick={(e) => {
                              e.preventDefault();
                              claimNFTsWithNFTC();
                            }}
                          >
                            {claimingNft ? "BUSY" : "MINT with NFTC"}
                          </StyledButton>
                        </s.Container>
                      </>
                    ) : (
                      <s.TextDescription
                        style={{
                          textAlign: "center",
                          fontSize: 28,
                          color: "var(--big-text)",
                        }}
                      >
                        The minting begins {new Date(CONFIG.LAUNCH_DATE).toUTCString()}.
                        {/* The minting ended {new Date(CONFIG.END_DATE).toUTCString()}. */}
                      </s.TextDescription>
                    )}
                  </>
                )}
              </>
            )}


            {/* {new Date() < new Date(CONFIG.END_DATE) ? (
            <s.TextDescription
                      style={{
                        textAlign: "center",
                        fontSize: 28,
                        color: "var(--big-text)",
                      }}
                    >
                      Minting is possible until {new Date(CONFIG.END_DATE).toUTCString()}.
                    </s.TextDescription>
                    ) : (
                      null
                    )} */}
            <div className="countdown" style={{
              textAlign: "center",
              fontSize: 48,
              fontWeight: "bold",
              fontFamily: "Liberation Mono",
              color: "var(--big-text)",
            }}>
              {/* <Countdown
                date={new Date(CONFIG.END_DATE)}
              >
                <Completionist />
              </Countdown> */}

              <Countdown
                date={new Date(CONFIG.LAUNCH_DATE)}
              >
                <Completionist />
              </Countdown>
            </div>
            <s.Container jc={"center"} ai={"center"} style={{ width: "90%" }}>
              <s.SpacerLarge />
              <s.TextDescription
                style={{
                  textAlign: "center",
                  color: "var(--muted-text)",
                }}
              >
                The gas limit is set to {CONFIG.GAS_LIMIT} to ensure a successful mint. It is recommended NOT to lower the
                gas limit, because the minting may fail with a lower gas limit. Unused gas is automatically returned to your wallet.
              </s.TextDescription>
              <s.SpacerSmall />
              <s.TextDescription
                style={{
                  textAlign: "center",
                  fontSize: 20,
                  color: "var(--highlighted-text)",
                }}
              >
                Please note: Once you make the purchase, you cannot undo this action.
              </s.TextDescription>
              <s.SpacerLarge />
              <s.Container flex={1} jc={"center"} ai={"center"}>
                <StyledImg alt={"preview"} src={"/config/images/preview.gif"} />
              </s.Container>
            </s.Container>
            <s.SpacerLarge />
            <s.Container
              jc={"center"}
              ai={"center"}
              style={{
                margin: "0",
                padding: "0",
                color: "#efefef",
              }}
            >
              {/* <a href="https://github.com/zquestz/reapers.cash" target="_blank"><img src="/github-alt-brands.svg" /></a> */}
              <div className="social">
                <a href="https://t.me/NFTClubOfficial" target="_blank"><img src="/telegram-plane-brands.svg" /></a>
                <a href="https://twitter.com/PixelGuyClub1" target="_blank"><img src="/twitter-brands.svg" /></a>
                <a href="https://www.instagram.com/nft_club_official/" target="_blank"><img src="/instagram-brands.svg" /></a>
                <a href="https://app.nftclub.cash" target="_blank"><img src="/bch.svg" /></a>
              </div>
            </s.Container>
          </s.Container>
          {/* <s.SpacerLarge />
          <s.Container flex={1} jc={"center"} ai={"center"}>
            <StyledImg
              alt={"preview"}
              src={"/config/images/preview.gif"}
            />
          </s.Container> */}
        </ResponsiveWrapper>

      </s.Container>

    </s.Screen>
  );
}

export default App;
